
a:link {
  color: gray;
  text-decoration: none;
}
a:visited {
  color: gray;
  text-decoration: none;
}
a:hover {
  color: black;
  text-decoration: none;
}
.component-fade-enter-active, .component-fade-leave-active {
  -webkit-transition: opacity .3s ease;
  transition: opacity .3s ease;
}
.component-fade-enter, .component-fade-leave-to {
  opacity: 0;
}
ul {
  list-style: none;
  line-height: 3em;
}
div {
  background-color: aliceblue;
}
img {
  max-width: 100%;
  height: auto !important;
  line-height: 0px;
}
.wrapper {
  display: grid;
  grid-template-columns: repeat(77, 1fr);
  grid-template-rows: repeat(100, 1fr);
  height: 100vh;
}
.one {
  grid-column: 12/78;
  grid-row: 1/28;
  border-radius: 5px 5px 5px 5px;
  background: linear-gradient(87deg, #5e72e4 0, #825ee4 100%);
  padding-top: 10px;
  padding-right: 5px;
  background-color: #5758bb;
  -webkit-box-shadow: 2px 4px 2px lightgray;
          box-shadow: 2px 4px 2px lightgray;
  font-weight: 500;
}
.two {
  grid-column: 15 / 45;
  grid-row: 23 / 50;
  border-radius: 5px 5px 5px 5px;
  background-color: #5758bb;
  -webkit-box-shadow: 0px 2px 0px lightgray;
          box-shadow: 0px 2px 0px lightgray;
}
.three {
  grid-column: 47/75;
  grid-row: 23 / 50;
  border-radius: 5px 5px 5px 5px;
  background-color: #5758bb;
  -webkit-box-shadow: 2px 2px 2px lightgray;
          box-shadow: 2px 2px 2px lightgray;
}
.four {
  grid-column: 1/11;
  grid-row: 1/100;
  background-color: #5758bb;
  -webkit-box-shadow: 2px 2px 2px lightgray;
          box-shadow: 2px 2px 2px lightgray;
}
li.menu {
  width: 100%;
  list-style: none;
  line-height: 3em;
  font-family: sans-serif;
}
li.menu:hover, li.menu:focus {
  background-color: #f2f2f2;
}
li.menu:active {
  font-weight: 700;
}
.friend-list li {
  width: 100%;
  list-style: none;
  line-height: 3em;
  font-family: sans-serif;
}
.friend-list li:hover, .friend-list li:focus {
  background-color: #f2f2f2;
}


.dropdown-content-enter-active,
.dropdown-content-leave-active {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
}
.dropdown-content-enter,
.dropdown-content-leave-to {
  opacity: 0;
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

